  src: url("../font/Manrope-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: 'Manrope';
  src: url("../font/Manrope-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal; }

@font-face {
