@keyframes bounce-x {
  0% {
    transform: translateX(0%); }
  25%, 75% {
    transform: translateX(60%); }
  50%, 100% {
    transform: translateX(20%); } }

@keyframes slide-down {
  0% {
    transform: translateY(-100%); }
  100% {
    transform: translateY(0%); } }

@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fly-1 {
  0%, 100% {
    transform: translate(0, 0); }
  20% {
    transform: translate(17px, 7px); }
  40% {
    transform: translate(34px, -10px); }
  60% {
    transform: translate(7px, -27px); }
  80% {
    transform: translate(-37px, -15px); } }

@keyframes fly-2 {
  0%, 100% {
    transform: translate(0, 0); }
  20% {
    transform: translate(-21px, -19px); }
  40% {
    transform: translate(-51px, 10px); }
  60% {
    transform: translate(-37px, -21px); }
  80% {
    transform: translate(41px, 5px); } }

@keyframes fly-3 {
  0%, 100% {
    transform: translate(0, 0); }
  20% {
    transform: translate(-16px, -5px); }
  40% {
    transform: translate(-17px, -26px); }
  60% {
    transform: translate(-37px, -15px); }
  80% {
    transform: translate(-4px, -17px); } }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.4;
  font-family: "Manrope", sans-serif;
  color: #64607D;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  -moz-osx-font-smoothing: grayscale; }

@media all and (min-width: 769px) {
  body {
    font-size: 18px; } }

h1 {
  margin: 0;
  font-size: 40px;
  line-height: 1; }

p {
  margin: 0; }

a {
  color: inherit;
  text-decoration: none; }

.container {
  padding: 0 15px;
  max-width: 1200px;
  margin: 0 auto;
  position: relative; }

.main__container {
  margin-top: 145px; }

.header {
  color: #000000;
  margin-top: 43px;
  z-index: 100;
  position: relative; }
  .header__container {
    display: flex;
    justify-content: space-between; }
  .header__logo {
    margin: auto 0;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    text-transform: uppercase; }
  .header__navigation {
    display: flex;
    list-style: none;
    padding: 0;
    font-style: normal;
    font-weight: 600;
    font-size: 14px; }
  .header__nav-main {
    margin-right: 26px; }
  .header__nav-main:last-child {
    margin-right: 0; }
  .header__arrow {
    margin-left: 12px;
    transform: rotate(0deg);
    transition: .2s;
    cursor: pointer; }
  .header__sublist {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    text-align: center;
    position: absolute;
    transition: .2s;
    transform: translateY(-10px);
    opacity: 0; }
  .header__nav-secondary {
    margin-top: 6px; }
  .header__right {
    margin: auto 0;
    font-style: normal;
    font-weight: 600;
    font-size: 14px; }
  .header__right-link {
    margin-right: 49px; }
  .header__right-link:last-child {
    margin-right: 0; }

.bg {
  position: absolute;
  width: calc(50% + 150px);
  top: 0;
  right: 0;
  height: 100%; }

.bg-polygon {
  position: absolute;
  top: 100px;
  left: 0; }

.visible {
  opacity: 1;
  transform: translateY(0px); }

.arrow-active {
  transform: rotate(180deg); }

.btn-violet {
  background-color: #3734A9;
  color: #ffffff;
  padding: 16px 35px;
  border-radius: 10px; }

.btn-violet:hover {
  background-color: #4F46BA;
  transition: all .2s ease-in-out; }

.btn-orange {
  padding: 16px 125px 14px 126px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #FF7F5C;
  border-radius: 5px; }

.greeting {
  color: #000000;
  display: flex; }
  .greeting__left {
    display: flex;
    flex-direction: column;
    padding-right: 123px; }
  .greeting__header {
    font-style: normal;
    font-weight: 800;
    font-size: 60px;
    line-height: 74px;
    margin-bottom: 19px; }
  .greeting__description {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.5em;
    color: #64607D; }
  .greeting__buttons {
    display: flex;
    margin-top: 47px; }
  .greeting__button {
    padding: 15px 36px;
    border-radius: 47px;
    margin-right: 50px; }
  .greeting__play-video {
    display: flex;
    line-height: 60px;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    color: #3734A9; }
  .greeting__play-svg {
    margin-right: 17px;
    cursor: pointer; }
  .greeting__title {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    margin: 20px 0 20px 0;
    color: #3734A9; }
  .greeting__form {
    display: flex;
    flex-direction: column; }
  .greeting__input {
    width: 312px;
    height: 26px;
    border-radius: 5px;
    border: 1px solid #DFE4F1;
    padding: 16px 21px;
    margin-bottom: 24px; }
